/*

Posts

*/

/**
 * Header
 */

.new-header {
  margin-top: $theme-space-unit * 3;
  margin-bottom: $theme-space-unit * 2;
  .site-title {
    font-size: 22px;
    border-bottom: 2px solid #ccc;
    margin-top: 0px;
  }
}
.site-title {
  font-family: $title-font-family;
  color: $grey-color-dark;
  font-size: 30px;
  font-weight: 300;
  line-height: 20px;
  letter-spacing: -1px;
  margin-top: 7px;
  margin-bottom: 0;

  &:visited {
    color: $grey-color-dark;
  }
  &:hover {
    text-decoration: none;
  }
}

/**
 * Post content
 */

.post {
  padding: $spacing-unit/2 0;

  .post-header {
    margin-bottom: $spacing-unit;
  }

  h1,h2,h3,h4,h5,h6 {
    line-height: 1;
    font-family: $theme-font-title;
    font-weight: 400;
  }

  h1 { font-size: 42px; line-height: 1.1em; }
  h2 { font-size: 28px; }
  h3 { font-size: 22px; }
  h4 { font-size: 18px; }

  .post-meta {
    font-size: $small-font-size;
    font-family: $theme-font-title;
    color: $grey-color;
  }

  .post-content {
    margin-bottom: $spacing-unit;
  }

  hr {
    display: inline-block;
    width: 50%;
    border: 1px solid rgb(223, 223, 223);
    margin: $spacing-unit/2 0;
  }

  .important {
    font-weight: 700;
    color: rgb(197, 41, 80);
  }

  .aside {
    color: $grey-color;
  }

  @include media-query($on-palm) {
    video {
      width: 100%;
    }
  }

}