/**
 * Site footer
 */
.site-footer {
    border-top: 1px solid $grey-color-light;
    padding: $spacing-unit 0;
}

.footer-heading {
    font-size: 18px;
    margin-bottom: $spacing-unit / 2;
    a {
      color: $text-color;
    }
}

.contact-list,
.social-media-list {
    list-style: none;
    margin-left: 0;
}

.footer-col-wrapper {
    font-size: 15px;
    color: $grey-color;
    margin-left: -$spacing-unit / 2;
    @extend %clearfix;
}

.footer-col {
    float: left;
    margin-bottom: $spacing-unit / 2;
}

.footer-col {
    padding-left: $spacing-unit / 2;
}

.footer-col-1 {
    width: -webkit-calc(35% - (#{$spacing-unit} / 2));
    width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
    width: -webkit-calc(20% - (#{$spacing-unit} / 2));
    width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
    width: -webkit-calc(45% - (#{$spacing-unit} / 2));
    width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
    .footer-col-1,
    .footer-col-2 {
        width: -webkit-calc(50% - (#{$spacing-unit} / 2));
        width:         calc(50% - (#{$spacing-unit} / 2));
    }

    .footer-col-3 {
        width: -webkit-calc(100% - (#{$spacing-unit} / 2));
        width:         calc(100% - (#{$spacing-unit} / 2));
    }
}

@include media-query($on-palm) {
    .footer-col {
        float: none;
        width: -webkit-calc(100% - (#{$spacing-unit} / 2));
        width:         calc(100% - (#{$spacing-unit} / 2));
    }
}

/**
 * Page content
 */
.page-content {
    position: relative;
}

/**
 * Posts
 */

.callout {
  box-sizing: border-box;
  position: fixed;
  padding: 40px;
  padding-right: 0px;
  width: 250px;
  top: 0;
  left: 0;
  bottom: 0;
  color: $grey-color-dark;
  font-size: 15px;
  @include media-query($on-palm) {
    position: relative;
    padding-right: 40px;
    width: 100%;
    top: auto;
    left: auto;
    bottom: auto;
  }
  .links {
    line-height: 1.7em;
  }
}
.posts {

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 32px 32px;

  padding: 32px;
  box-sizing: border-box;
  margin-left: 250px;
  width: calc(100% - 252px);
  position: relative;
  @include media-query($on-big-laptop) {
    grid-template-columns: 1fr;
  }
  @include media-query($on-palm) {
    margin-left: 0;
    width: 100%;
  }
  @include media-query($on-small-palm) {
    padding: 0px;
  }
}

video {
  max-width: 100%;
}

/* gallery */
.image-gallery {
  width: 100%;

  .image-container {
    overflow: hidden;
    display: inline-block;
    float: left;
    width: calc(25% - 10px); /* Half of margin (below) */
    padding-top: calc(25% - 10px);
    position: relative;
    margin: 5px;
    > img {
      position: absolute;
      top: 0;
      height: 100%;
      width: initial !important;
      max-width: none !important;
      display: block;
    }
    @include media-query($on-laptop) {
        width: calc(33.33% - 10px);
        padding-top: calc(33.33% - 10px);
    }
    @include media-query($on-palm) {
        width: calc(50% - 10px);
        padding-top: calc(50% - 10px);
    }
  }
}
