/**
 * Syntax highlighting styles
 */

.highlight {
    background: #fff;
    @extend %vertical-rhythm;

    .highlighter-rouge & {
      background: #eef; // Jump to "Code formatting" in _base.scss to change this value.
    }

    > figcaption {
      border: 1px solid black;
      padding: 2px;
      padding-left: 12px;
      border: 1px solid darken($grey-color-light, 10%);
      border-bottom: none;
      border-radius: $border-radius $border-radius 0px 0px;
      font-family: 'Courier', monospace;
      color: $brand-color;
    }

