/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}



/**
 * Basic styling
 */
body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background-color: $background-color;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
    @include media-query($on-palm) {
        font-size: $mobile-font-size;
    }
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}



/**
 * Images
 */
img {
    max-width: 100%;
    vertical-align: middle;
}



/**
 * Figures
 */
// figure > img {
//     display: block;
// }
//
// figcaption {
//     font-size: $small-font-size;
// }



/**
 * Lists
 */
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
         margin-bottom: 0;
    }
}



/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
    font-family: $base-font-family;
    font-weight: $base-font-weight;
}



/**
 * Links
 */
a {
    color: $brand-color;
    text-decoration: none;

    &:visited {
        color: darken($brand-color, 15%);
    }

    &:hover {
        color: $text-color;
        text-decoration: underline;
    }
}



/**
 * Blockquotes
 */
blockquote {
    color: $grey-color;
    border-left: 4px solid $grey-color-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    letter-spacing: -1px;
    font-style: italic;

    > :last-child {
        margin-bottom: 0;
    }
}



/**
 * Code formatting
 * Find syntax related css in ./_syntax-highlighting.css
 */
pre,
code {
    font-family: 'Courier', monospace;
    font-size: 15px;
    border: 1px solid darken($grey-color-light, 10%);
    border-radius: 0px 0px $border-radius $border-radius;
    // background-color: #eeeeff; // default
    background-color: #f7f7f7;
    // background-color: #f3f7f9;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}

.notice {
  padding: 12px;
  background-color: #fffbe8;
  border: 1px solid #ffee9c;
  border-radius: $border-radius;
  margin-bottom: $spacing-unit / 2;
}


/**
 * Wrapper
 */
.wrapper {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
    max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    position: relative;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
        max-width:         calc(#{$content-width} - (#{$spacing-unit}));
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}


/**
 * Clearfix
 */
%clearfix {

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
 * Icons
 */
.icon {

    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $grey-color;
        }
    }

    &.big-icon {
        > svg {
          width: 32px;
          height: 32px;
        }
    }

    &.match-base-font {
        height: $base-font-size;
        vertical-align: -3px;
    }
}


h1, h2, h3 {
  img {
    padding-left: 10px;
    height: 16px;
  }
}

@mixin transition($transition...) {
  -moz-transition:    $transition;
  -o-transition:      $transition;
  -webkit-transition: $transition;
  transition:         $transition;
}

@mixin shadow($shadow) {
  -moz-box-shadow:    $shadow;
  -webkit-box-shadow: $shadow;
  box-shadow:         $shadow;
}

@font-face{
	font-family: 'HKGrotesk';
	src: url('../assets/hkgrotesk/HKGrotesk.eot');
	src: url('../assets/hkgrotesk/HKGrotesk.eot?iefix') format('eot'),
	     url('../assets/hkgrotesk/HKGrotesk.woff') format('woff'),
	     url('../assets/hkgrotesk/HKGrotesk.ttf') format('truetype'),
	     url('../assets/hkgrotesk/HKGrotesk.svg#webfont') format('svg');
}
